import React from 'react';
import res1 from './assets/res1.png';
import res2 from './assets/res2.png';
import res3 from './assets/res3.png';
import res4 from './assets/res4.png';
import res5 from './assets/res5.png';
import './App.css';
import { Modal } from 'antd';


class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      showVaccineModal: false,
    }


    this.iconsList = [
      {
        icon: res2,
        name: 'Make Vaccine',
        group: 1,
        onClickHandler: this.showModal,
      },
      {
        icon: res5,
        name: 'Coding to Save World',
        group: 4,
        onClickHandler: this.naviToScratch3,
      },
      {
        icon: res3,
        name: 'About Virus',
        group: 2,
        onClickHandler: this.naviToAboutVirus,
      },
      {
        icon: res4,
        name: 'Defeat Virus',
        group: 3,
        onClickHandler: this.naviToDefeatVirus,
      }
    ];

    this.vaccineList = [
      {
        icon: res2,
        name: 'Vaccine A',
        nextPage: '/scratch1.html',
      },
      {
        icon: res2,
        name: 'Vaccine B',
        nextPage: '/scratch2.html',
      },
      {
        icon: res2,
        name: 'Vaccine C',
        nextPage: '/scratch3.html',
      },
      {
        icon: res2,
        name: 'Vaccine D',
        nextPage: '/scratch2.html',
      },
      {
        icon: res2,
        name: 'Vaccine E',
        nextPage: '/scratch1.html',
      },

    ]
  }

  getIconItemClassName(group) {
    switch (group) {
      case 1:
        return "groupOne";
      case 2:
        return "groupTwo";
      case 3:
        return "groupThree";
      default:
        return "groupFour";
    }
  }

  naviToAboutVirus = () =>{
    window.open(process.env.PUBLIC_URL+'/aboutvirus.html')
  }

  naviToDefeatVirus = () => {
    window.open(process.env.PUBLIC_URL+'/defeatvirus.html')
  }

  naviToScratch3 = () => {
    window.open(process.env.PUBLIC_URL+'/scratch3')
  }


  showModal = () => {
    this.setState({
      showVaccineModal: true,
    });
  };

  handleOk = e => {
    // console.log(e);
    this.setState({
      showVaccineModal: false,
    });
  };

  handleCancel = e => {
    // console.log(e);
    this.setState({
      showVaccineModal: false,
    });
  };


  renderIconItemList = (iconList) => {
    // console.log(iconList);
    return iconList.map((element, idx) => {
      return (
        <div className="iconItem" key={idx} onClick={element.onClickHandler}>
          <div className={this.getIconItemClassName(element.group)}>
            <img src={element.icon} className="iconImg" alt="icon Not Available" />
          </div>
          <div className="iconName">{element.name}</div>
        </div>
      )

    })
  }

  openGamePage = (item) => {
    window.open(item.nextPage);
  }

  renderVaccineItemList = (vaccineList) => {
    // console.log(iconList);
    return vaccineList.map((element, idx) => {
      return (
        <div className="vaccineItem" key={idx}
        //  onClick={this.openGamePage.bind(this,element)}
        >
          <a className="vaccineName" href={process.env.PUBLIC_URL + element.nextPage} target="_blank" rel="noopener noreferrer">{element.name}</a>
        </div>
      )

    })
  }



  // main page
  render() {
    return (
      <div className="pageContent">
        <Modal
          title="Choose vaccine"
          visible={this.state.showVaccineModal}
          footer={null}
          closable={false}
          onCancel={this.handleCancel}
          width="80%"
        >
          <div className="vaccineSection">
            {
              this.renderVaccineItemList(this.vaccineList)
            }
          </div>
        </Modal>
        <div className="mainContent">
          <div className="header">Using Code To Defeat Virus</div>
          <div className="body">
            <div className="left">
              <img className="leftImage" src={res1} alt="background" />
            </div>
            <div className="right">
              {
                this.renderIconItemList(this.iconsList)
              }
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default App;
